___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --todo-bg: #324b3e;
    /* Mint */
    --project-bg: #587d71;
    /* Sage Green */
    --project: #f4ede0;
    /* Light Beige */
    --board-bg: #f8f5e9;
    /* Pure White */
    --board-border: #cc6972;
    /* Coffee Brown */
    --text: #5f3b35;
    /* Rich Brown */
    scroll-behavior: smooth;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: var(--text);
    font-family: "Covered By Your Grace", cursive;
}

body {
    background-color: #344E41;
    display: flex;
    height: 100vh;
}

.hidden {
    display: none !important;
}

.projectContainer {
    z-index: 9999;
    display: flex;
    overflow: auto;
    flex-direction: column;
    height: 100%;
    width: 20%;
    min-width: 300px;
    padding: 50px 10px 50px 10px;
    background-color: var(--project-bg);
}

.projectContainer .add {
    margin: 20px;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
}

#projectList {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 25px;
}

#projectList li {
    background:
        linear-gradient(to left top, transparent 50%, rgba(255, 228, 193, 0.753) 0) no-repeat 100% 100% / 3em 3em,
        linear-gradient(to right bottom, transparent 0%, var(--project-bg) 0) no-repeat 100% 100% / 3em 3em;
    background-color: var(--project);
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 87%;
    height: 250px;
    position: relative;
    cursor: pointer;
    font-size: 1.3rem;
    letter-spacing: 2px;
}

#projectList li:hover {
    transform: scale(1.05);
}

.projectTitle {
    text-transform: uppercase;
}

#projectList li img {
    cursor: pointer;
    top: 0;
    left: 0;
    padding: 13px;
    position: absolute;
    color: black;

}

#projectList li img:hover {
    transform: scale(1.1);
}

.addProject {
    color: #2c3033;
    font-family: 'Courier New', Courier, monospace;
    font-size: 4rem !important;
    align-self: center;
}

.addProject:hover {
    transform: scale(1.1);
    cursor: pointer;
}

.deleteProject {
    color: white;
    font-size: 1.5rem;
}

#addProject {
    background-color: rgb(78, 37, 37);
    display: flex;
    flex-direction: column;
    margin: auto;
    justify-content: center;
    align-items: center;
    position: absolute;
    z-index: 99999;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 300px;
    width: 530px;
    border-radius: 20px;
    gap: 15px;
    box-shadow: 0 0 0 100vh rgba(78, 78, 78, 0.555);
}

#addProject>* {
    font-size: 1.1rem;
    text-transform: uppercase;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    width: 76%;
    border-radius: 7px;
    border: none;
    padding: 15px;
}
.exitProject, h3,button{
    color: #faece0;
}
#addProject .exitProject {
    font-size: 1.4rem;
    margin-left: 5px;
    width: fit-content;
    border: none;
    background-color: transparent;
    position: absolute;
    top: 0;
    left: 0;
}

#addProject h3,
#editModal h3 {
    margin-bottom: 20px;
    border: none;
    text-align: center;
    font-size: 2rem;
    padding: 0;
}

#addProject button {
    cursor: pointer;
    background-color: var(--project-bg);
}

#addProject button:hover {
    transform: scale(1.01);
}

.toDoContainer {
    background-color: var(--todo-bg);
    object-fit: contain;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1;
}

.board {
    margin-right: 50px;
    overflow: auto;
    background-color: #1F1E33;
    border-top: 30px solid var(--board-border);
    border-bottom: 30px solid var(--board-border);
    border-right: 30px solid var(--board-border);
    border-left: 30px solid var(--board-border);
    border-radius: 20px;
    height: 85%;
    width: 85%;
    min-width: 450px;
    max-width: 670px;
    position: relative;
    background:
        linear-gradient(to left top, transparent 50%, rgb(219, 187, 172) 0) no-repeat 100% 100% / 3em 3em,
        linear-gradient(to right bottom, transparent 0%, var(--board-border) 0) no-repeat 100% 100% / 3em 3em;
    background-color: var(--board-bg);
}

.board::-webkit-scrollbar {
    width: 5px;
}

.board::-webkit-scrollbar-track {
    background: #93b3ac;
}

.board::-webkit-scrollbar-thumb {
    background: #779992;
    border-radius: 6px;
}

.board::-webkit-scrollbar-thumb:hover {
    background: #75948d;
}

.clipContainer {
    position: absolute;
    right: 25%;
    left: 25%;
}

.clipTop {
    height: 10px;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    width: 50%;
    margin: auto;
    display: block;
    background-color: #f28583;
}

.clipBottom {
    height: 20px;
    border-radius: 12px;
    width: 80%;
    margin: auto;
    display: block;
    background-color: #f28583;
}

#toDoList {
    padding: 50px 43px 50px 43px;
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

#toDoList h2 {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 2rem;
    text-transform: uppercase;
}

.addToDo {
    font-size: 4rem;
    text-align: center;
    cursor: pointer;
    width: fit-content;
    margin: auto;
    margin-bottom: 40px;
    bottom: 0;
}

.addToDo:hover {
    transform: scale(1.1);
}

#addToDo {
    display: flex;
    flex-direction: column;
    margin: auto;
    justify-content: center;
    align-items: center;
    position: absolute;
    z-index: 99999;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-height: 550px;
    min-width: 600px;
    border-radius: 20px;
    gap: 15px;
    box-shadow: 0 0 0 100vh rgba(78, 78, 78, 0.555);
    background-color: rgb(78, 37, 37);
}

#addToDo>* {
    font-size: 1.1rem;
    text-transform: uppercase;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    width: 70%;
    border-radius: 7px;
    border: 1px solid #638685;
    padding: 15px;
}

#addToDo button[type="submit"] {
    background-color: var(--project-bg);
    border: none;
    color: #faece0;
    cursor: pointer;
}

#addToDo button[type="submit"]:hover {
    transform: scale(1.01);
}

#addToDo h3 {
    margin-bottom: 20px;
    border: none;
    text-align: center;
    font-size: 2rem;
    padding: 0;
}

.exitToDo,
.exitEdit {
    font-size: 1.4rem !important;
    margin-left: 5px;
    width: fit-content !important;
    border: none !important;
    padding: 0;
    background-color: transparent;
    border: none;
    color: #faece0;
    position: absolute;
    top: 0;
    left: 0;
}

.exitToDo:hover,
.exitEdit:hover {
    transform: scale(1.1);
    cursor: pointer;
}

#toDoList li {
    letter-spacing: 1px;
    font-size: 1.5rem;
    display: flex;
    justify-content: space-between;
    min-height: 50px;
    width: 100%;
    gap: 5px;
}

.toDoDataContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    border-bottom: 2px solid;
}

.deleteToDo:hover,
.editToDo:hover,
.expandToDo:hover {
    transform: scale(1.1);
    cursor: pointer;
}

.checkBox {
    cursor: pointer;
    text-align: center;
    align-self: flex-end;
    width: 55px;
    height: 45px;
    border: 2px solid;
    display: block;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.toDoData {
    word-break: break-all;
}

.checked {
    font-size: 2rem;
    position: absolute;
    height: 6rem;
}

.priority {
    height: 2.3rem;
}

.expandToDo {
    height: 3rem;
    cursor: pointer;
}

.optionsContainer {
    display: flex;
    margin: auto;
}

.toDoDiv {
    display: flex;
    gap: 15px;
}

li {
    display: flex;
    flex-direction: column;
}

.description {
    width: 90%;
    word-wrap: break-word;
    margin-left: 50px;
    text-decoration: underline;
}

#editModal {
    background-color: rgb(78, 37, 37);
    display: flex;
    flex-direction: column;
    margin: auto;
    justify-content: center;
    align-items: center;
    position: absolute;
    z-index: 99999;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-height: 530px;
    width: 550px;
    border-radius: 20px;
    gap: 15px;
    box-shadow: 0 0 0 100vh rgba(78, 78, 78, 0.555);
}

#editModal>* {
    font-size: 1.1rem;
    text-transform: uppercase;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    width: 67%;
    border-radius: 7px;
    border: 1px solid #638685;
    padding: 15px;
    color: #faece0;
}

#editModal button[type="submit"] {
    cursor: pointer;
    background-color: var(--project-bg);
    color: white;
}

input[type="text"],
input[type="date"],
input[type="number"] {
    background-color: #e6d5c7 !important;
    color: var(--text) !important;
    font-weight: bold;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #8ec5c2;
}

/* Handle */
::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background: #ffffff;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #f0f0f0;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;IAClB,SAAS;IACT,qBAAqB;IACrB,eAAe;IACf,kBAAkB;IAClB,gBAAgB;IAChB,mBAAmB;IACnB,eAAe;IACf,uBAAuB;IACvB,iBAAiB;IACjB,eAAe;IACf,eAAe;IACf,uBAAuB;AAC3B;;AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,kBAAkB;IAClB,6CAA6C;AACjD;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,aAAa;AACjB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,aAAa;IACb,cAAc;IACd,sBAAsB;IACtB,YAAY;IACZ,UAAU;IACV,gBAAgB;IAChB,4BAA4B;IAC5B,mCAAmC;AACvC;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI;;2GAEuG;IACvG,gCAAgC;IAChC,gBAAgB;IAChB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,UAAU;IACV,aAAa;IACb,kBAAkB;IAClB,eAAe;IACf,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,eAAe;IACf,MAAM;IACN,OAAO;IACP,aAAa;IACb,kBAAkB;IAClB,YAAY;;AAEhB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,cAAc;IACd,8CAA8C;IAC9C,0BAA0B;IAC1B,kBAAkB;AACtB;;AAEA;IACI,qBAAqB;IACrB,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,iCAAiC;IACjC,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,uBAAuB;IACvB,mBAAmB;IACnB,kBAAkB;IAClB,cAAc;IACd,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,aAAa;IACb,YAAY;IACZ,mBAAmB;IACnB,SAAS;IACT,+CAA+C;AACnD;;AAEA;IACI,iBAAiB;IACjB,yBAAyB;IACzB,6EAA6E;IAC7E,UAAU;IACV,kBAAkB;IAClB,YAAY;IACZ,aAAa;AACjB;AACA;IACI,cAAc;AAClB;AACA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,kBAAkB;IAClB,YAAY;IACZ,6BAA6B;IAC7B,kBAAkB;IAClB,MAAM;IACN,OAAO;AACX;;AAEA;;IAEI,mBAAmB;IACnB,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,UAAU;AACd;;AAEA;IACI,eAAe;IACf,mCAAmC;AACvC;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,gCAAgC;IAChC,mBAAmB;IACnB,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,OAAO;AACX;;AAEA;IACI,kBAAkB;IAClB,cAAc;IACd,yBAAyB;IACzB,0CAA0C;IAC1C,6CAA6C;IAC7C,4CAA4C;IAC5C,2CAA2C;IAC3C,mBAAmB;IACnB,WAAW;IACX,UAAU;IACV,gBAAgB;IAChB,gBAAgB;IAChB,kBAAkB;IAClB;;6GAEyG;IACzG,iCAAiC;AACrC;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,kBAAkB;AACtB;;A